import React, { PureComponent } from "react";
import PropTypes from "prop-types";
import {
  Panel,
  PanelHeader,
  FormLayout,
  FormLayoutGroup,
  Input,
  Select,
  Button
} from "@vkontakte/vkui";
import HeadButton from "../../commons/buttons/HeadButton";

import "./ExtendedFilters.css";

class ExtendedFilters extends PureComponent {
  static propTypes = {
    goBack: PropTypes.func,
    back: PropTypes.string,
    handleFormSubmit: PropTypes.func.isRequired
  };

  state = {
    fields: {
      category: {
        type: "select",
        top: "",
        options: [{ id: "123", value: "", name: "" }],
        name: "category",
        placeholder: " ",
        value: null
      },
      salaryFrom: {
        type: "text",
        placeholder: "",
        name: "salaryFrom",
        value: ""
      },
      salaryTo: {
        type: "text",
        placeholder: "",
        name: "salaryTo",
        value: ""
      },
      experience: {
        type: "select",
        top: " ",
        options: [],
        name: "experience",
        placeholder: "  ",
        value: null
      },
      employmentType: {
        type: "select",
        top: " ",
        options: [],
        name: "employmentType",
        placeholder: "  ",
        value: null
      },
      schedules: {
        type: "select",
        top: " ",
        options: [],
        name: "schedules",
        placeholder: "  ",
        value: null
      }
    }
  };

  handleBackClick = () => {
    this.props.goBack();
  };

  renderButton = <HeadButton onClick={this.handleBackClick} />;

  handleChange = e => {
    const { name, value } = e.currentTarget;
    this.setState(prevState => {
      const field = { ...prevState.fields[name], value };

      return {
        fields: {
          ...prevState.fields,
          [name]: field
        }
      };
    });
  };

  handleFormSubmit = () => {
    this.props.handleFormSubmit(this.state.fields);
  };

  render() {
    const { id } = this.props;
    const {
      salaryFrom,
      salaryTo,
      category,
      experience,
      employmentType,
      schedules
    } = this.state.fields;

    return (
      <Panel id={id}>
        <PanelHeader left={this.renderButton}> </PanelHeader>
        <FormLayout>
          <Button onClick={this.handleFormSubmit} size="xl">
             
          </Button>
          <Select {...category} onChange={this.handleChange}>
            {category.options.map(cat => {
              return (
                <option key={cat.id} value={cat.value}>
                  {cat.name}
                </option>
              );
            })}
          </Select>
          <FormLayoutGroup top=" ">
            <Input {...salaryFrom} onChange={this.handleChange} />
            <Input {...salaryTo} onChange={this.handleChange} />
          </FormLayoutGroup>
          <Select {...experience} onChange={this.handleChange} />
          <Select {...employmentType} onChange={this.handleChange} />
          <Select {...schedules} onChange={this.handleChange} />
          <div className="extendedfilters__search">
            <button onClick={this.handleFormSubmit}></button>
          </div>
        </FormLayout>
      </Panel>
    );
  }
}

export default ExtendedFilters;
