export const errors = [
  {
    message: "     ",
    validate: values => {
      const { vkUser, vacations } = values;
      return !!(vkUser && !vkUser.isAbleToCreateVacations);
    }
  },
  {
    message:
      "    .       !",
    validate: values => {
      const { vkUser, vacations } = values;
      return !!(
        vkUser &&
        vkUser.vacationsLimit &&
        Array.isArray(vacations) &&
        vkUser.vacationsLimit < vacations.length
      );
    }
  }, {
    message: '   ',
    validate: (values) => {
      const { vkUser } = values;
      return !vkUser;
    }
  }
];
